import {connect} from 'react-redux'

const Messages = ({messages}) => {
    let prevMsg = '';
    return ( 
        <div className="w-full flex flex-col">
            {messages.map((m, i) => 
            {
                try{
                    const isDiffSender = prevMsg != m.identity;
                    return ( <div key={i} className={`${m.messageCreatedByMe ? "justify-end items-end" : "justify-start"} flex flex-col`}>
                {isDiffSender ?<div className="font-semibold mx-2 text-black">
                   {m.messageCreatedByMe ? 'You' : m.identity}
                    </div> : null}
                <div className={`${m.messageCreatedByMe ? 'bg-blue-600 text-white' : 'bg-slate-300 text-black'} rounded-md p-2 w-[80%] ${isDiffSender ? 'mt-2' : 'mt-1'}`}>
                    {m.content}
                    </div>
                </div>)
                }
                finally{
                    prevMsg = m.identity;
                }
            }
                
            )}
        </div>
     );
}
 const mapStoreStateToProps = (state) => {
    return {
        ...state
    }
 }
export default connect(mapStoreStateToProps)(Messages);