import img from '../res/Hop.svg'
import { useNavigate } from 'react-router-dom';


function Home() {
  
  const navigate = useNavigate();
  const handleCreateRoom = () => {
    navigate('./join-room/true')
  }
  const handleJoinRoom = () => {
    navigate('./join-room/')
  }
  return (
    <div className="h-screen flex flex-col justify-center items-center bg-slate-200">
        <div className="flex flex-col justify-center bg-white w-[80%] md:w-[60%] rounded-md ring-4 ring-blue-600 ring-inset">
          
      <p className='py-2 px-2 text-4xl text-gray-700 font-bold self-center'>HopOn Video Conferencing</p>
            <img src={img} className='w-52 h-52 my-4 mx-auto '></img>
            <div className='flex justify-evenly mx-4 mb-4 flex-wrap gap-3'>
              <button className='py-1 px-2 text-xl outline outline-2 outline-blue-600 rounded-md w-20' onClick={() => handleJoinRoom()}>Join</button>
              <button className='py-1 px-2 text-xl bg-blue-600 text-slate-50 rounded-md w-20' onClick={() => handleCreateRoom()}>Create</button>
            </div>
        </div>
  </div>
  )
}

export default Home;
