import { useState } from "react";
import send from '../res/send.svg'
import * as webRTCHandler from '../utils/webRTCHandler'
function InputBox() {

    const [message, setMessage] = useState('');

    const handleOnChange = (e) => {
        setMessage(e.target.value);
    }
    const handleSend = () => {
        if(message)
        {
            webRTCHandler.sendMessageUsingDataChannel(message)
            setMessage("")
        }
    }
    const handleKeyPressed = (event) => {
        if(event.key === "Enter")
        {
            event.preventDefault();
            handleSend();
        }
    }
    
    return ( <div className="flex items-center w-full p-1 bg-slate-200">
        <input
        value={message}
        className="py-1 flex-1 px-2 rounded-md placeholder:text-sm"
        placeholder="Enter your message"
        onChange={handleOnChange} onKeyDown={handleKeyPressed}
        ></input>
        <img src={send} className="mx-2 w-7 h-7 basis-1/6" onClick={handleSend}></img>
    </div> );
}

export default InputBox;