import { useParams } from "react-router-dom";
import { connect } from "react-redux";
import { setRoomHost } from "../app/actions";
import { useEffect, useState } from "react";
import JoinRoomContent from "./JoinRoomContent";

const JoinRoomPage = (props) => {
  const { setIsRoomHostAction } = props;
  const isRoomHost = useParams().isRoomHost ?? false;
  const [roomIdValue, setRoomIdValue] = useState("");
  const [nameValue, setNameValue] = useState("");
  useEffect(() => {
    setIsRoomHostAction(isRoomHost);
  }, []);

  return (
    <div className="h-screen flex flex-col justify-center items-center bg-slate-300">
      <div 
          className="w-[60%] flex flex-col bg-slate-50 justify-center items-center outline outline-1">
            <p className="text-xl p-2 my-2 ">{isRoomHost ? "Create a meeting" : "Join a meeting"}</p>
        <JoinRoomContent
          roomIdValue={roomIdValue}
          nameValue={nameValue}
          setRoomIdValue={setRoomIdValue}
          setNameValue={setNameValue}
          isRoomHost={isRoomHost}
        />
      </div>
    </div>
  );
};

const mapStoreStateToProps = (state) => {
  return {
    ...state,
  };
};
const mapActionsToProps = (dispatch) => {
  return {
    setIsRoomHostAction: (isRoomHost) => dispatch(setRoomHost(isRoomHost)),
  };
};

export default connect(mapStoreStateToProps, mapActionsToProps)(JoinRoomPage);
