import { store } from "../app/store";

function ParticipantsSection() {
    const users = store.getState().participants;
    return ( <div className="w-[20%] bg-slate-100">
        <h1 className="mt-2 text-center text-2xl font-semibold font-[Lato]">Participants</h1>
        <div className="p-2 divide-y-2 gap-1"> 
        {users.map((user) => {
            return <h3 key={user.identity} className="p-2 text-center border-b-[1px] border-black">{user.identity}</h3>
        })}
        </div>
        
    </div> );
}

export default ParticipantsSection;