import io from 'socket.io-client';
import { store } from '../app/store';
import { setParticipants, setRoomId } from '../app/actions';
import * as webRTCHandler from './webRTCHandler'
const SERVER = 'https://hopon.azurewebsites.net';

let socket = null;

export const connectWithSocketIOServer = () => {
    socket = io(SERVER);
    socket.on("connect", () => {
    })
    socket.on("room-id", (data) => {
        const {roomId} = data;
        store.dispatch(setRoomId(roomId));
    })
    socket.on("room-update", (data) => {
        store.dispatch(setParticipants(data.connectedUsers));
    })
    socket.on("conn-prepare", (data) => {
        const {connUserSocketId} = data;
        webRTCHandler.prepareNewPeerConnection(connUserSocketId, false);
        socket.emit('conn-init', {connUserSocketId : connUserSocketId})
    })
    socket.on('conn-signal', (data) => {
        webRTCHandler.handleSignalingData(data);
    })
    socket.on('conn-init', (data) => {
        const {connUserSocketId} = data;
        webRTCHandler.prepareNewPeerConnection(connUserSocketId, true);
    })
    socket.on('user-disconnected', (data) => {
        webRTCHandler.removePeerConnection(data);
    })

}

export const createRoom = (identity, onlyAudio) => { 
    const data = {
        identity,
         onlyAudio
    }

    socket.emit('create-room', data);
}
export const joinRoom = (identity, roomId, onlyAudio) => {
    const data = {
        identity,
        roomId,
        onlyAudio
    }
    
    socket.emit('join-room', data);
}

export const signalPeerData = (data) => {
    socket.emit('conn-signal', data);
}