import { setMessages, setShowOverlay } from "../app/actions";
import { store } from "../app/store";
import * as wss from "./wss"
import Peer from 'simple-peer'
//import {Peer, Add} from 'simple-peer'
const defaultConstraints = {
    audio: true,
    video: {
        width: "480",
        height: "360"
    }
}
const audioOnlyContraints = {
    audio : true,
    video : false
}
let localStream;

export async function getLocalPreviewInitRoomConnection(isRoomHost, identity, roomId = null, onlyAudio) {
    const constraint = onlyAudio ? audioOnlyContraints : defaultConstraints;
    
    navigator.mediaDevices.getUserMedia(constraint)
    .then(stream => {
        localStream = stream;
        showLocalVideoPreview(localStream)
        store.dispatch(setShowOverlay(false))
        isRoomHost ? wss.createRoom(identity, onlyAudio) : wss.joinRoom(identity, roomId, onlyAudio)
    }).catch(err => console.log(err));
}

const showLocalVideoPreview = (stream) =>  {
    const videosContainer = document.getElementById('videos_portal');
    videosContainer.classList.add('ml-[20%]', 'absolute', 'w-[60%]', 'h-cal[(100vh-50px)]', 'left-0','top-0', 'flex', 'items-center','justify-center')
    const videoContainer = document.createElement('div');
    videoContainer.classList.add('video_track_container');
    const videoElement = document.createElement('video');
    videoElement.srcObject = stream;
    videoElement.autoplay = true;
    videoElement.muted = true;

    videoElement.onloadedmetadata = () => {
        videoElement.play();
    }
    videoContainer.appendChild(videoElement);

    if(store.getState().OnlyWithAudio)
    {
        videoContainer.appendChild(getAudioOnlyLabel());
    }

    videosContainer.appendChild(videoContainer);
}

let peers = {};
let streams = [];

const getConfiguration = () => {
    return {
        iceServers: [
            {
                urls : 'stun:stun.l.google.com:19302'
            }
        ]
    }
}

export const removePeerConnection = (data) => {
    const {socketId} = data;
    const videoContainer = document.getElementById(socketId);
    const videoEl = document.getElementById(`${socketId}-video`);

    if(videoContainer && videoEl)
    {
        const tracks = videoEl.srcObject.getTracks();
        tracks.forEach(t => t.stop());
        videoEl.srcObject = null;
        videoContainer.removeChild(videoEl);
        videoContainer.parentNode.removeChild(videoContainer);

        if(peers[socketId])
        {
            peers[socketId].destroy();
        }
        delete peers[socketId];
    }
}

const messengerChannel = 'messenger';

const getAudioOnlyLabel = () => {
    const labelContainer = document.createElement('div')
    labelContainer.classList.add('w-full', 'h-full', 'flex', 'items-center','justify-center',
    'absolute','z-[99]','top-0','left-0'); 

    const label = document.createElement('p');
    label.classList.add('text-lg', 'text-black');
    label.innerHTML = 'Only with Audio';
    labelContainer.appendChild(label);
    return labelContainer;
}

export const prepareNewPeerConnection = (connUserSocketId, isInitiator) => {
    const configuration = getConfiguration();
    peers[connUserSocketId] = new Peer({
        initiator : isInitiator,
        config : configuration,
        stream : localStream,
        channelName: messengerChannel
    })

    peers[connUserSocketId].on('signal', (data) => {
        const signalData = {
            signal : data,
            connUserSocketId : connUserSocketId
        }
        wss.signalPeerData(signalData);
    })

    peers[connUserSocketId].on('stream', (stream) => {
        addStream(stream, connUserSocketId);
        streams = [...streams, stream];
    })
    peers[connUserSocketId].on('data', (data) => {
        const messageData = JSON.parse(data);
        appendNewMessage(messageData);
    })
}

export const handleSignalingData = (data) => {
    peers[data.connUserSocketId].signal(data.signal);
}
const addStream = (stream, connUserSocketId) => {
    const videosContainer = document.getElementById('videos_portal');
    
    const videoContainer = document.createElement('div');
    videoContainer.id = connUserSocketId;
    videoContainer.classList.add('video_track_container');
    const videoElement = document.createElement('video');
    videoElement.srcObject = stream;
    videoElement.autoplay = true;
    videoElement.muted = false;
    videoElement.id = `${connUserSocketId}-video`

    
    videoElement.onloadedmetadata = () => {
        videoElement.play();
    }

    videoContainer.addEventListener('click', () => {
        if(videoContainer.classList.contains('w-full')){
            videoContainer.classList.remove('absolute', 'w-full', 'h-full', 'top-0', 'left-0');
        }else{
            videoContainer.classList.add('absolute', 'w-full', 'h-full', 'top-0', 'left-0');
        }
    })
    videoContainer.appendChild(videoElement);
    const participants = store.getState().participants;
    const participant = participants.find(p => p.socketId === connUserSocketId);

    if(participant?.onlyAudio)
    {
        videoContainer.appendChild(getAudioOnlyLabel());
    }

    videosContainer.appendChild(videoContainer);
}

export const toggleMic = (isMuted) => {
    localStream.getAudioTracks()[0].enabled = !isMuted;
}
export const toggleCamera = (videoEnabled) => {
    localStream.getVideoTracks()[0].enabled = videoEnabled;
}


export const sendMessageUsingDataChannel = (messageContent) => {
    
    const identity = store.getState().identity;

    const localMessageData = {
        content : messageContent,
        identity,
        messageCreatedByMe : true
    }
    appendNewMessage(localMessageData);

    const messageData = {
        content: messageContent,
        identity
    };
    const stringifiedMessageData = JSON.stringify(messageData);
    for(let socketId in peers)
    {
        peers[socketId].send(stringifiedMessageData);
    }

}


const appendNewMessage = (messageData) => {
    const messages = store.getState().messages;
    store.dispatch(setMessages([...messages, messageData]));
};

export default getLocalPreviewInitRoomConnection;