import { useEffect, useRef, useState } from "react";
import {
  setConnectOnlyWithAudio,
  setIdentity,
  setRoomId,
} from "../app/actions";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
import { getRoomExists } from "../utils/API";
const Input = ({ value, placeholder, changeHandler, autofocus }) => 
  
    (autofocus ?<input
      className="p-1 w-[80%] my-2 mx-10 outline outline-1 rounded-md"
      value={value}
      autoFocus
      placeholder={placeholder}
      onChange={changeHandler}
    /> : <input
    className="p-1 w-[80%] my-2 mx-10 outline outline-1 rounded-md"
    value={value}
    placeholder={placeholder}
    onChange={changeHandler}
  /> )
function JoinRoomContent(props) {
  const navigate = useNavigate();
  const {
    roomIdValue,
    nameValue,
    setRoomIdValue,
    setNameValue,
    isRoomHost,
    OnlyWithAudio,
    setConnectOnlyWithAudio,
    setIdentity,
    setRoomId
  } = props;
  const [errorMessage, setErrorMessage] = useState("");
  const handleNameChange = (e) => {
    setNameValue(e.target.value);
  };
  const handleRoomIdChange = (e) => {
    setRoomIdValue(e.target.value);
  };

  const handleOnlyWithAudio = (e) => {
    setConnectOnlyWithAudio(!OnlyWithAudio);
  };
  const joinRoom = async () => {
    const response = await getRoomExists(roomIdValue);
    const { roomExists, full } = response;
    if (roomExists) {
      if (full) {
        setErrorMessage("Room is full. Try again later");
      } else {
        setRoomId(roomIdValue);
        navigate("/HopOn/room");
      }
    } else {
      setErrorMessage("Room does not exist. Check the meeting id.");
    }
  };

  const createRoom = () => {
    navigate("/HopOn/room");
  };

  const handleCreateOrJoin = () => {
    
    setIdentity(nameValue)
    if (!isRoomHost) {
      joinRoom();
    } else {
      createRoom();
    }
  };
  return (
    <>
      {!isRoomHost && (
        <Input
          value={roomIdValue}
          placeholder="Enter meeting ID"
          autofocus={true}
          changeHandler={handleRoomIdChange}
        ></Input>
      )}
      <Input
        value={nameValue}
        autofocus={!isRoomHost}
        placeholder="Enter your name"
        changeHandler={handleNameChange}
      ></Input>
      {errorMessage && (
        <p className="text-red-600 font-semibold w-[80%]">{errorMessage}</p>
      )}
      <div className="flex gap-2">
        <input
          className="p-2 mt-1"
          type="checkbox"
          onClick={handleOnlyWithAudio}
        ></input>
        <span>Only with Audio</span>
      </div>

      <div className="flex w-[90%] mt-4 justify-end">
        <button
          className="py-2 px-4 bg-sky-400 outline outline-1 mx-2 mb-2 rounded"
          onClick={handleCreateOrJoin}
        >
          Join
        </button>
        <button
          className="py-2 px-4 outline outline-1 mx-2 mb-2 rounded"
          onClick={() => navigate(-1)}
        >
          Cancel
        </button>
      </div>
    </>
  );
}
const mapStoreStateToProps = (state) => {
  return { ...state };
};

const mapActionsToProps = (dispatch) => {
  return {
    setConnectOnlyWithAudio: (OnlyWithAudio) =>
      dispatch(setConnectOnlyWithAudio(OnlyWithAudio)),
    setIdentity: (identity) => dispatch(setIdentity(identity)),
    setRoomId: (roomId) => dispatch(setRoomId(roomId)),
  };
};

export default connect(
  mapStoreStateToProps,
  mapActionsToProps
)(JoinRoomContent);
