import { store } from "../app/store";
import { useState } from "react";
import camera from "../res/camera.svg";
import cameraOff from "../res/cameraOff.svg";
import mic from "../res/mic.svg";
import micOff from "../res/micOff.svg";
import * as webRTCHandler from "../utils/webRTCHandler";
import copy from "../res/copy.svg"
import { useNavigate } from "react-router-dom";
function VideosSection() {
  const [micMuted, setMicMuted] = useState(true);
  const [videoEnabled, setVideoEnabled] = useState(true);
  const [displayCopy, setDisplayCopy] = useState(true);
  const navigate = useNavigate();

  const roomId = store.getState().roomId;
  const handleMicButtonPressed = () => {
    webRTCHandler.toggleMic(micMuted);
    setMicMuted(!micMuted);
  };
  const handleCameraButtonPressed = () => {
    webRTCHandler.toggleCamera(videoEnabled);
    setVideoEnabled(!videoEnabled);
  };

  const handleCopyRoomId =() => {
    navigator.clipboard.writeText(roomId);
    setDisplayCopy(false);
    setTimeout(() => setDisplayCopy(true), 2000);
  }
  const handleDisconnect = () => {
    const siteURL = window.location.origin;
    window.location.href = siteURL + '/HopOn';
  }
  return (
    <div className="w-[60%] relative flex flex-col">
      <div className="flex justify-center items-center p-2 bg-gradient-to-b from-sky-500 to-blue-800 text-white self-center left-1/4 right-1/4 rounded-b-lg z-20">
        RoomID {displayCopy ? <img src={copy} style={{color: "white"}} onClick={() => handleCopyRoomId()} className="w-5 h-5 ml-2"></img>: <div className="text-green-400 ml-2 text-md">Copied</div>}
      </div>
      <div className="flex-1 mb-2"></div>
      <div className="self-center w-[60%] bg-gradient-to-b from-sky-500 to-blue-800 px-2 py-2 rounded-t-md flex justify-center items-center gap-4">
        <img
          src={videoEnabled ? cameraOff : camera}
          style={{padding : "10px"}}
          onClick={handleCameraButtonPressed}
        ></img>
        <img src={micMuted ? micOff : mic} style={{padding : "10px"}} onClick={handleMicButtonPressed}></img>
        <button className="px-3 py-1 bg-red-600 rounded-l-full rounded-r-full hover:bg-red-500 rounded-md text-white" onClick={handleDisconnect}>
          Leave Room
        </button>
      </div>
    </div>
  );
}
//mic button with state
//video button with state
//leave room button with window.location.href set to window.location.origin
//screen share buttin with setscreensharing state

export default VideosSection;
