import { useEffect } from "react";
import ChatsSection from "./ChatsSection";
import Overlay from "./Overlay";
import ParticipantsSection from "./ParticipantsSection";
import VideosSection from "./VideosSection";
import {connect} from 'react-redux';
import * as webRTCHandler from '../utils/webRTCHandler'
function Roompage({showOverlay, isRoomHost, identity, roomId, OnlyWithAudio}) {
    useEffect(() => {
        if(!(isRoomHost || roomId))
        {
            const siteUrl = window.location.origin;
            window.location.href = siteUrl;
        }else
        webRTCHandler.getLocalPreviewInitRoomConnection(isRoomHost, identity, roomId, OnlyWithAudio)
    }, [])
    return (  
        <div className="flex h-screen w-full overflow-hidden">
            <ParticipantsSection></ParticipantsSection>
            <VideosSection></VideosSection>
            <ChatsSection></ChatsSection>
            {showOverlay && <Overlay/>}
        </div>
    );
}

const mapStateToProps = (state) => {
    return {...state};
}
export default connect(mapStateToProps)(Roompage);